___CSS_LOADER_EXPORT___.push([module.id, "#content[data-v-00490f10]{margin-top:7%}", "",{"version":3,"sources":["webpack://./resources/js/pages/user/Profiles.vue"],"names":[],"mappings":"AA6XA,0BACA,aACA","sourcesContent":["<template>\r\n  <div id=\"main\">\r\n    <div id=\"header\">\r\n      <HeaderComponent />\r\n    </div>\r\n    <div class=\"container\" id=\"content\">\r\n      <div v-if=\"this.loading\" class=\"d-flex align-items-center\">\r\n        <strong>Loading...</strong>\r\n        <div\r\n          class=\"spinner-border ms-auto\"\r\n          role=\"status\"\r\n          aria-hidden=\"true\"\r\n        ></div>\r\n      </div>\r\n      <section v-else class=\"home-section\">\r\n        <div class=\"container-fluid\">\r\n          <div class=\"row flex-lg-nowrap\">\r\n            <div class=\"col\">\r\n              <div class=\"row\">\r\n                <div class=\"col mb-3\">\r\n                  <div class=\"card\">\r\n                    <div class=\"card-body\">\r\n                      <form\r\n                        @submit=\"updateProFile\"\r\n                        enctype=\"multipart/form-data\"\r\n                        oninput='password_confirmation.setCustomValidity(password_confirmation.value != password.value ? \"Passwords do not match.\" : \"\")'\r\n                      >\r\n                        <div class=\"e-profile\">\r\n                          <div class=\"row\">\r\n                            <div class=\"col-12 col-sm-auto mb-3\">\r\n                              <div class=\"mx-auto\" style=\"width: 140px\">\r\n                                <img\r\n                                  v-if=\"DataUser.user_photo\"\r\n                                  :src=\"DataUser.user_photo\"\r\n                                  class=\"img-profile rounded-circle\"\r\n                                  alt=\"\"\r\n                                  width=\"140px;\"\r\n                                  height=\"140px;\"\r\n                                />\r\n                                <img\r\n                                  v-else\r\n                                  src=\"/assets/backend/img/avatar/avatar-1.png\"\r\n                                  class=\"img-profile rounded-circle\"\r\n                                  alt=\"\"\r\n                                  width=\"140px;\"\r\n                                  height=\"140px;\"\r\n                                />\r\n                              </div>\r\n                            </div>\r\n                            <div\r\n                              class=\"\r\n                                col\r\n                                d-flex\r\n                                flex-column flex-sm-row\r\n                                justify-content-between\r\n                                mb-3\r\n                              \"\r\n                            >\r\n                              <div\r\n                                class=\"text-center text-sm-left mb-2 mb-sm-0\"\r\n                              >\r\n                                <h4 class=\"pt-sm-2 pb-1 mb-0 text-nowrap\">\r\n                                  {{ DataUser.name }}\r\n                                </h4>\r\n                                <p class=\"mb-0\">@johnny.s</p>\r\n                                <div class=\"text-muted\">\r\n                                  <small>Last seen 2 hours ago</small>\r\n                                </div>\r\n                                <div class=\"mt-2\">\r\n                                  <button class=\"btn btn-info\" type=\"button\">\r\n                                    <!-- <i class=\"fa fa-fw fa-camera\"></i>\r\n                                    <span>Change Photo</span> -->\r\n                                    <input\r\n                                      type=\"file\"\r\n                                      @change=\"onFileImageChange\"\r\n                                      name=\"user_photo\"\r\n                                      accept=\"image/jpeg, image/png\"\r\n                                      multiple\r\n                                    />\r\n                                  </button>\r\n                                </div>\r\n                              </div>\r\n                              <div class=\"text-center text-sm-right\">\r\n                                <span class=\"badge badge-secondary\"\r\n                                  >administrator</span\r\n                                >\r\n                                <div class=\"text-muted\">\r\n                                  <small>Joined 09 Dec 2017</small>\r\n                                </div>\r\n                              </div>\r\n                            </div>\r\n                          </div>\r\n                          <ul class=\"nav nav-tabs\">\r\n                            <li class=\"nav-item\">\r\n                              <a href=\"\" class=\"active nav-link\">Settings</a>\r\n                            </li>\r\n                          </ul>\r\n                          <div class=\"tab-content pt-3\">\r\n                            <div class=\"tab-pane active\">\r\n                              <div class=\"row\">\r\n                                <div class=\"col\">\r\n                                  <div class=\"row\">\r\n                                    <div class=\"col\">\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >ชื่อ - นามสกุล</label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"FormProfile.name\"\r\n                                          class=\"form-control\"\r\n                                          type=\"text\"\r\n                                          name=\"name\"\r\n                                          placeholder=\"\"\r\n                                          required\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                    <div class=\"col\">\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >ไอดีผู้ใช้</label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"FormProfile.username\"\r\n                                          class=\"form-control\"\r\n                                          type=\"text\"\r\n                                          name=\"username\"\r\n                                          placeholder=\"\"\r\n                                          required\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                  </div>\r\n                                  <div class=\"row\">\r\n                                    <div class=\"col\">\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >อีเมล์</label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"FormProfile.email\"\r\n                                          class=\"form-control\"\r\n                                          type=\"text\"\r\n                                          name=\"email\"\r\n                                          placeholder=\"user@example.com\"\r\n                                          required\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                    <div class=\"col\">\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >เบอร์โทร</label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"FormProfile.phone\"\r\n                                          class=\"form-control\"\r\n                                          type=\"text\"\r\n                                          name=\"phone\"\r\n                                          placeholder=\"\"\r\n                                          required\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                  </div>\r\n                                </div>\r\n                              </div>\r\n                              <div class=\"row\">\r\n                                <div class=\"col-12 col-sm-6 mb-3\">\r\n                                  <div class=\"mb-2\">\r\n                                    <b>Change Password</b>\r\n                                  </div>\r\n                                  <div class=\"row\">\r\n                                    <div\r\n                                      v-if=\"FormProfile.password_confirmation\"\r\n                                      class=\"col\"\r\n                                    >\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >New Password</label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"FormProfile.password\"\r\n                                          class=\"form-control required\"\r\n                                          type=\"password\"\r\n                                          name=\"password\"\r\n                                          placeholder=\"••••••\"\r\n                                          required\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                    <div v-else class=\"col\">\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >New Password</label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"FormProfile.password\"\r\n                                          class=\"form-control required\"\r\n                                          type=\"password\"\r\n                                          name=\"password\"\r\n                                          placeholder=\"••••••\"\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                  </div>\r\n                                  <div class=\"row\">\r\n                                    <div class=\"col\">\r\n                                      <div class=\"form-group\">\r\n                                        <label style=\"float: left\"\r\n                                          >Confirm\r\n                                          <span class=\"d-none d-xl-inline\"\r\n                                            >Password</span\r\n                                          ></label\r\n                                        >\r\n                                        <input\r\n                                          v-model=\"\r\n                                            FormProfile.password_confirmation\r\n                                          \"\r\n                                          class=\"form-control\"\r\n                                          type=\"password\"\r\n                                          name=\"password_confirmation\"\r\n                                          placeholder=\"••••••\"\r\n                                        />\r\n                                      </div>\r\n                                    </div>\r\n                                  </div>\r\n                                </div>\r\n                              </div>\r\n                              <div class=\"row\">\r\n                                <div class=\"col d-flex justify-content-end\">\r\n                                  <button class=\"btn btn-primary\">\r\n                                    Save Changes\r\n                                  </button>\r\n                                </div>\r\n                              </div>\r\n                            </div>\r\n                          </div>\r\n                        </div>\r\n                      </form>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </section>\r\n    </div>\r\n\r\n    <div id=\"footer\">\r\n      <FooterComponent />\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script scoped>\r\nimport axios from \"axios\";\r\nimport HeaderComponent from \"../../components/HeaderComponent.vue\";\r\nimport FooterComponent from \"../../components/FooterComponent.vue\";\r\nexport default {\r\n  name: \"dashboard\",\r\n  components: { HeaderComponent, FooterComponent },\r\n  data() {\r\n    return {\r\n      loading: true,\r\n      DataUser: {},\r\n      FormProfile: {\r\n        name: \"\",\r\n        username: \"\",\r\n        email: \"\",\r\n        phone: \"\",\r\n        password: \"\",\r\n        password_confirmation: \"\",\r\n      },\r\n      file_photo: \"\",\r\n    };\r\n  },\r\n  mounted() {\r\n    if (this.$store.state.token !== \"\") {\r\n      axios\r\n        .get(\"/api/auth/user-profile/\", {\r\n          headers: {\r\n            \"Content-Type\": \"application/json\",\r\n            Authorization: \"Bearer \" + this.$store.state.token,\r\n          },\r\n        })\r\n        .then((res) => {\r\n          this.loading = false;\r\n\r\n          //   alert(\"Axios Dashboard Success!!\");\r\n          for (let i = 0; i < res.data.length; i++) {\r\n            this.DataUser = {\r\n              id: res.data[i].id,\r\n              name: res.data[i].name,\r\n              email: res.data[i].email,\r\n              username: res.data[i].username,\r\n              phone: res.data[i].phone,\r\n              user_photo: res.data[i].user_photo,\r\n            };\r\n          }\r\n          this.FormProfile = {\r\n            id: this.DataUser.id,\r\n            name: this.DataUser.name,\r\n            email: this.DataUser.email,\r\n            username: this.DataUser.username,\r\n            phone: this.DataUser.phone,\r\n          };\r\n          //   console.log(this.FormProfile);\r\n        })\r\n        .catch((err) => {\r\n          //   alert(\"error axios Dashboard!!\");\r\n          this.$store.commit(\"clearToken\");\r\n          this.$router.push(\"/learning/login\");\r\n        });\r\n    } else {\r\n      //   alert(\"error this.$store.state.token!!\");\r\n      this.loading = false;\r\n      this.$router.push(\"/learning/login\");\r\n    }\r\n  },\r\n  methods: {\r\n    logout() {\r\n      axios\r\n        .post(\"/api/auth/logout\", { token: this.$store.state.token })\r\n        .then((res) => {\r\n          this.$store.commit(\"clearToken\");\r\n          this.$router.push(\"/learning/login\");\r\n        })\r\n        .catch((err) => {\r\n          this.error = \"Error!!\";\r\n        });\r\n    },\r\n    onFileImageChange(e) {\r\n      const file = e.target.files[0];\r\n      this.DataUser.user_photo = URL.createObjectURL(file);\r\n\r\n      //   this.filename = \"Selected File: \" + e.target.files[0].name;\r\n      this.file_photo = e.target.files[0];\r\n      console.log(this.file_photo);\r\n    },\r\n    updateProFile(e) {\r\n      e.preventDefault();\r\n\r\n      const config = {\r\n        headers: {\r\n          \"content-type\": \"multipart/form-data\",\r\n          \"X-CSRF-TOKEN\": document.querySelector('meta[name=\"csrf-token\"]')\r\n            .content,\r\n        },\r\n      };\r\n\r\n      let formData = new FormData();\r\n      formData.append(\"user_photo\", this.file_photo);\r\n      formData.append(\"name\", this.FormProfile.name);\r\n      formData.append(\"username\", this.FormProfile.username);\r\n      formData.append(\"email\", this.FormProfile.email);\r\n      formData.append(\"phone\", this.FormProfile.phone);\r\n      if (this.FormProfile.password) {\r\n        formData.append(\"password\", this.FormProfile.password);\r\n        formData.append(\r\n          \"password_confirmation\",\r\n          this.FormProfile.password_confirmation\r\n        );\r\n      }\r\n\r\n      // update original!\r\n      axios\r\n        .post(\"/api/update_profile/\" + this.DataUser.id, formData, config)\r\n        .then((res) => {\r\n          this.$router.go();\r\n        })\r\n        .catch((err) => {\r\n          this.error = \"Error!!\";\r\n        });\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n#content {\r\n  margin-top: 7%;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
